<template>
  <section class="authorize">
    <div class="container">
      <div class="back">
        <div class="back-arrow">
          <img src="@/assets/img/arrow-w.svg" />
        </div>
        <p class="back-subtitle">Назад</p>
      </div>

      <div class="form">
        <div class="form-title">Регистрация</div>

        <form
          class="form-wrapper"
          id="registration"
          @submit="checkForm"
          action="https://vuejs.org/"
          method="post"
        >
          <div class="input-wrapper">
            <input class="input input-withIcon" v-model="user.login" required />
            <label class="input-label">Логин</label>

            <img class="input-img" src="@/assets/img/header-logo.svg" />
          </div>

          <div class="input-wrapper">
            <input class="input input-withIcon" v-model="user.user" required />
            <label class="input-label">Почтовый адрес</label>

            <svg width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:class="'input-img'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M17.468 0c.858 0 1.681.346 2.288.961.606.615.947 1.45.947 2.32V8.05a3.677 3.677 0 00-1.294-.172V5.199l-8.729 5.21a.641.641 0 01-.551.05l-.106-.05-8.73-5.207v8.58c0 .522.205 1.023.57 1.392.363.37.857.577 1.372.577h7.096a4.233 4.233 0 00-.66 1.312H3.235c-.858 0-1.68-.346-2.288-.961A3.305 3.305 0 010 13.782v-10.5c0-.87.34-1.706.947-2.32A3.212 3.212 0 013.235 0h14.233zm0 1.313H3.235c-.515 0-1.009.207-1.373.576-.364.37-.568.87-.568 1.392v.397l9.057 5.405 9.058-5.407v-.395c0-.522-.204-1.023-.568-1.392a1.927 1.927 0 00-1.373-.576zM11.62 16.244l6.25-6.34a2.419 2.419 0 011.71-.718 2.39 2.39 0 011.712.72 2.486 2.486 0 01-.001 3.472l-6.25 6.338c-.364.37-.82.631-1.32.758l-1.938.491a1.135 1.135 0 01-1.093-.307 1.172 1.172 0 01-.303-1.108l.485-1.966c.124-.507.383-.97.748-1.339z" fill="#83B5B8"/></svg>
          </div>

          <div class="input-wrapper">
            <input
              class="input input-withIcon"
              v-model="user.password"
              required
            />
            <label class="input-label">Пароль</label>

            <img class="input-img" src="@/assets/img/lock-icon.svg" />
          </div>

          <div class="input-wrapper">
            <input
              class="input input-withIcon"
              v-model="user.password"
              required
            />
            <label class="input-label">Повторите пароль</label>

            <img class="input-img" src="@/assets/img/lock-icon.svg" />
          </div>

          <button @click="submit" class="button button-g form-wrapper__item">
            Зарегистироваться
          </button>
          <router-link class="button-router" to="/login">
            <button class="button button-white form-wrapper__item">
              Авторизоваться
            </button>
          </router-link>
        </form>
      </div>
    </div>
  </section>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      errors: [],
      user: {
        mail: "",
        login: "",
        password: "",
        passwordSecond: "",
      },
    };
  },
  methods: {
    ...mapActions("users", {
      addUser: "addUser",
    }),
    submit() {
      this.addUser(this.user);
    },
    checkForm(e) {
      if (this.user.login && this.user.password) {
        return true;
      }

      this.errors = [];

      if (!this.user.login) {
        this.errors.push("Требуется указать логин.");
      }
      if (!this.user.password) {
        this.errors.push("Требуется указать пароль.");
      }

      e.preventDefault();
    },
  },
};
</script>

<style lang="scss" scoped>
.authorize {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;

  width: 100%;
  height: 100%;
  background: url("@/assets/img/authorize__background.png");
  background-size: cover;
  .button-router {
    margin: 0 auto;
  }
  .back {
    display: flex;
    align-items: center;
    margin: 40px;
    &-arrow {
      width: 30px;
    }
    p {
      margin-left: 10px;
      color: #fff;
    }
  }

  .form {
    margin: 10% auto;
    width: 400px;
    padding: 20px;

    background: $gradient-w;
    border-radius: 10px;
    border: none;
    box-shadow: $shadow-big;
    &-title {
      margin: 20px 0;

      text-align: center;
      font-size: 24px;

      color: $text-grey;
    }
    &-wrapper {
      display: flex;
      justify-content: center;
      flex-direction: column;
      &__item {
        position: relative;

        width: 300px;
        margin: 10px auto;

        font-size: 18px;
      }
      &__icon {
        position: absolute;
        right: 0;
      }
      .button {
        height: 40px;
      }
    }
  }
}
.input-icon {
  position: absolute;
  top: 0;
}
</style>